
.row{
    margin-left: 1rem;
    color: white;
}

.row_posters_container{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 0.8rem;
}

.row_posters_container::-webkit-scrollbar{
    display: none;
}

.row_poster{
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    margin-right: 0.5rem;
    transition:transform 450ms;
}

.row_poster:hover{
    transform: scale(1.08);
    opacity: 1;
}

.large_poster{
    max-height: 250px;
}


.large_poster:hover{
    transform: scale(1.09);
    opacity: 1;
}